import { Component, OnInit, TemplateRef } from '@angular/core';
import { BsModalService,BsModalRef } from 'ngx-bootstrap/modal';
import { DialogService } from '../dialogue.service';
import { HttploginService } from '../httplogin.service';
import { Employee } from '../model/employee';
import { ToastrService  } from 'ngx-toastr';
@Component({
  selector: 'app-home',
  templateUrl: './home.component.html',
  styleUrls: ['./home.component.css']
})
export class HomeComponent implements OnInit {
  //myusername:string='';
  p:number=1;
  nameSearch:string='';
  empdata:any[]=[];
  isradiocheck:boolean=false;
  modalRef: BsModalRef;
  empobj=<Employee>{};
  config = {
    animated: true,
    
    ignoreBackdropClick: true,
    class: "alert alert-danger"
  };

  constructor(private service: HttploginService,
     private modalservice:BsModalService, 
     private dialogservice:DialogService,
     private toaster:ToastrService  ) { }

  ngOnInit() {
    //this.username=sessionStorage.getItem("username");
      this. getDataFromBackend()
  }

  getDataFromBackend()
  {
      this.service.getEmployee().subscribe((response)=>{
        this.empdata=(<any>response)
        console.log(this.empdata);
      })
  }

  onRadio(item)
  {
    this.isradiocheck=true;
     console.log(item);
     this.empobj=item;
  }

  radioCheck()
  {
    if(this.isradiocheck)
    {
    return true;
    }
    else
    {
    return false;
    }
  }

  onUpdate(popup:TemplateRef<any>)
  {
      if(this.radioCheck())
      {
        this.modalRef = this.modalservice.show(popup, this.config);
      }
      else
      {
        alert("please select a record to update");
      }
      
  }
  onDelete()
  {
    if(this.radioCheck())
    {
      
    this.dialogservice.OpenConfirmDialog("Are you sure to delete the record?")
    .afterClosed().subscribe((res)=>{
      console.log(res);
      if(res)
      {
        //delete a record
        this.service.DeleteRecord(this.empobj.id).subscribe((response)=>{
          //console.log(response)
          this.toaster.success(response);
          this.getDataFromBackend();
        })
      }
    })
    }
    else{
      alert("Please select a record to delete")
    }
  }
}
