import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { HttploginService } from '../httplogin.service';
import { Employee } from '../model/employee';

@Component({
  selector: 'app-employeedetails',
  templateUrl: './employeedetails.component.html',
  styleUrls: ['./employeedetails.component.css']
})
export class EmployeedetailsComponent implements OnInit {
 
  empobj=<Employee>{}
  constructor(private route:ActivatedRoute,private service:HttploginService) { }

  ngOnInit() {
    this.getDataFromUrl();
  }

  getDataFromUrl()
  {
      this.route.paramMap.subscribe((param)=>{
      
        //console.log(param);

        this. getDataFromBackend(param.get("id"))
      })
  }

  getDataFromBackend(id)
  {
           this.service.getEmployeeById(id).subscribe((response)=>{
            console.log(response);

            this.empobj=(<Employee>response)
           })
  }

  OnUpdate()
  {

  }
}
