import { Component, Input, OnInit } from '@angular/core';
import { HttploginService } from '../httplogin.service';
import { Employee } from '../model/employee';

@Component({
  selector: 'app-updateemployee',
  templateUrl: './updateemployee.component.html',
  styleUrls: ['./updateemployee.component.css']
})
export class UpdateemployeeComponent implements OnInit {
  
  allCountry:any[]=[];
  @Input() parentData=<Employee>{};

  issubmitDissabled:boolean=true;
  backendResponse:any="";
  constructor(private service:HttploginService) { }

  ngOnInit() {
    this.GetAllCountry();
  }
  GetAllCountry(){
    this.service.getCountry()
    .subscribe((response)=>{
      this.allCountry=(<any>response);
    })
  }

  onupdate()
  {
    this.parentData.updatedDate=Date.now();
    this.parentData.updatedBy=sessionStorage.getItem("username");
    this.service.UpdateEmployee(this.parentData).subscribe((response)=>{
      console.log(response);
      this. issubmitDissabled=false;
      this.backendResponse=response;
    })
  }
}
